* {
    box-sizing: border-box;
}

body {
    height: 100vh;
    margin: 0;
}

.content {
    display: grid;
    justify-content: center;
    align-items: center;

    min-height: 100vh;
}

h1 {
    margin: 0;
    padding: 0;

    font-size: 120px;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}

button {
    background-color: #555;

    display: inline-block;

    padding: 15px 32px;

    font-size: 16px;
    color: white;
    text-align: center;
    text-decoration: none;

    border: 1px solid #fff;
}

button:active {
    background-color: #000;
}

.copy {
    position: absolute;
    right: 1rem;
    bottom: 1rem;
}

.new {
    position: absolute;
    bottom: 1rem;
    left: 1rem;
}

.copied {
    position: absolute;
    top: 0;
    left: 0;

    background-color: rgba(0, 0, 0, .7);

    display: none;

    height: 100vh;
    width: 100vw;

    font-weight: bold;
    font-size: 150px;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
    line-height: 100vh;
    color: white;
    text-align: center;
}
